import React from 'react'
import "./index.scss"
import DefaultBaseInfiniteTable from "../../../Components/01Table/00defaultBaseInfiniteTable.jsx";
import request from "../../../Shared/RequestWrapper.jsx";

const DefaultShowRoomType = function(props){
    var {item} = props;
    console.log(item);
    return (
        <div className="col-lg-4 col-md-4 col-sm-6 col-xs-12">
            <div className="roomCard">
                <img src={"/"+item.Photo}/>
                <p className="title">{item.RoomType} ({item.Pax})</p>
                <div className="buttonRoom"><a href="#"> Book </a></div>
            </div>
        </div>
    )
}


const DefaultShowPromo = function(props){
    var {item} = props;
    console.log(item);
    return (
        <div className="col-lg-4 col-md-4 col-sm-6 col-xs-12">
            <div className="hvrbox">
                <img src={"/"+item.Photo}/>
                <p className="title">{item.Name}</p>
                <div className="overlay"></div>
                <div className="button"><a href="#"> Read More </a></div>
            </div>
        </div>
    )
}


export default class ReservationDetailContainer extends React.Component{

    static defaultProps ={
        limitPage:5,
        baseUrl:"api/outlet/view",
        isShowHidden: true,
        isInfiniteScroll: true
    }

    loadData = () => {
        if (this.state.componentStatus != 'loading') {
            var id = this.state.outlet_detail_id;
            if (this.state.componentStatus != 'done') {
                this.setState({
                    componentStatus: 'loading'
                });

                request({
                    url:    this.state.baseUrl,
                    method: 'GET',
                }).then(response => {
                    this.setState({
                        componentStatus: 'ready',
                        outletData: response.data.data.data
                    });

                    this.loadPromo();
                })

            }
        }
    }

    loadRoomTypes = () => {
        if (this.state.componentStatus != 'loading') {
            var outlet = this.state.outletData.Code;
            if (this.state.componentStatus != 'done') {
                this.setState({
                    componentStatus: 'loading'
                });
                request({
                    url:    '/api/roomtype/findByOutlet',
                    method: 'GET',
                    params:{
                        'outlet' : outlet
                    }
                }).then(response => {
                    console.log(response.data.data);
                    this.setState({
                        componentStatus: 'ready',
                        roomTypeData: response.data.data.data
                    });
                })
            }
        }
    }

    loadPromo = () => {
        if (this.state.componentStatus != 'loading') {
            var outlet = this.state.outletData.Code;
            if (this.state.componentStatus != 'done') {
                this.setState({
                    componentStatus: 'loading'
                });
                request({
                    url:    '/api/promo/findByOutlet',
                    method: 'GET',
                    params:{
                        'outlet' : outlet
                    }
                }).then(response => {
                    console.log(response.data.data);
                    this.setState({
                        componentStatus: 'ready',
                        promoData: response.data.data.data
                    });

                    this.loadRoomTypes();
                })
            }
        }
    }

    constructor(){
        super();
        var outlet_detail_id = window.outlet_detail_id ? window.outlet_detail_id : 0;
        const element = document.getElementById('outletDetailContainer')
        const props = Object.assign({}, element.dataset)
        this.state = {
            slideData:[{
                id: 1,
                image: 'http://react-responsive-carousel.js.org/assets/3.jpeg',
                title : 'Title 1'
            }],
            componentStatus:'ready',

            // Data
            outletData: {},
            foodData: [],
            roomTypeData: [],
            promoData: [],
            type: 'card',
            outlet_detail_id: outlet_detail_id,
            baseUrl:"api/outlet/view/"+outlet_detail_id,

        }
        this.loadData();
    }

    render() {
        return (
            <div className={"outletDetail"}>
                <header className="text-white headerBG">
                    <div className="text-center">
                        <h2 className="outletName">{this.state.outletData.Name}</h2>
                    </div>
                </header>
                <div className={"container-detail-outlet"}>

                    <div className={"banner-detail-outlet"} style={{  backgroundImage: "url(/"+this.state.outletData.Photo+")",
                        backgroundPosition: 'center',
                        backgroundSize: 'cover',
                        backgroundRepeat: 'no-repeat',
                        height:'400px'}}></div>

                    <div id="promo" className="wow fadeIn">
                        <h2 className="text-black">
                            <hr className="lineIndicatorBlue"/>
                            Promos On This Outlet </h2>
                        <div className={"promoInOutlet row"}>
                            <DefaultBaseInfiniteTable
                                contentShown={<DefaultShowPromo />}
                                contentHidden={<span/>}
                                header={<span/>}
                                data={this.state.promoData}
                                callMoreData={this.loadPromo} {...this.state}
                                footer={ <span/> }/>
                        </div>
                    </div>
                    <hr/>

                    <div id="room" className="wow fadeIn">
                        <h2 className="text-black">
                            <hr className="lineIndicatorBlue"/>
                            Available Room </h2>
                        <div className={"roomInOutlet row"}>
                            <DefaultBaseInfiniteTable
                                contentShown={<DefaultShowRoomType />}
                                contentHidden={<span/>}
                                header={<span/>}
                                data={this.state.roomTypeData}
                                callMoreData={this.loadRoomTypes} {...this.state}
                                footer={ <span/> }/>
                        </div>
                    </div>

                </div>
                <br />
            </div>
        )
    }
}
