import React from 'react'
import "./index.scss"
import DemoCarousel from "~/0Template/Components/00Default/06Carousel/00demoCarousel.jsx"
import request from "../../../Shared/RequestWrapper.jsx";

export default class NewsDetailContainer extends React.Component{

    static defaultProps ={
        limitPage:5,
        baseUrl:"api/news/view",
        isShowHidden: true,
        isInfiniteScroll: true
    }


    loadData = () => {
        if (this.state.componentStatus != 'loading') {
            console.log(this.state.baseUrl);

            var id = this.state.news_detail_id;

            if (this.state.componentStatus != 'done') {
                this.setState({
                    componentStatus: 'loading'
                });

                request({
                    url:    this.state.baseUrl,
                    method: 'GET',
                }).then(response => {
                    console.log(response.data.data);
                    this.setState({
                        componentStatus: 'ready',
                        newsData: response.data.data.data
                    });
                })

            }
        }
    }


    constructor(){
        super();

        var news_detail_id = window.news_detail_id ? window.news_detail_id : 0;

        const element = document.getElementById('newsDetailContainer')
        const props = Object.assign({}, element.dataset)

        this.state = {
            slideData:[{
                id: 1,
                image: 'http://react-responsive-carousel.js.org/assets/3.jpeg',
                title : 'Title 1'
            }],
            componentStatus:'ready',

            // Data
            newsData: {},
            news_detail_id: news_detail_id,
            baseUrl:"api/news/view/"+news_detail_id,

        }

        this.loadData();
    }

    render() {
        return (
            <div className={"newsDetail"}>
                <header className="text-white headerBG">
                    <div className="text-center">
                        <h2 className="newsTitle">{this.state.newsData.Title}</h2>
                    </div>
                </header>
                <div className={"container-detail-news"}>
                    <div className={"banner-detail-news"} style={{  backgroundImage: "url(/"+this.state.newsData.URL+")",
                        backgroundPosition: 'center',
                        backgroundSize: 'contain',
                        backgroundRepeat: 'no-repeat',
                        height:'400px'}}></div>
                    <br />
                    <div className="content-detail-news col-md-12">
                        <p>{this.state.newsData.Description}</p>
                    </div>

                </div>
                <br />
            </div>
        )
    }
}
