import React, { Component } from "react";
import "./index.scss";
import {FieldText, FieldSelectOption} from "../../../Components/03Form/FieldType.jsx";
import {ErrorMessage, Form, Formik} from "formik";

class ReservationBookingContainer extends React.Component{
    constructor() {
        super();

        var hours = [];
        for (var i=0; i<12; i++){
            var hour = i+1;
            var name = "Hour";
            if (hour > 1) name = "Hours";
            hours[i] = {
                "key" : hour,
                "value" : hour+" "+name
            }
        }

        this.state = {
            urlRegister: "/reservations/booking",
            initialValue: {
                outlet: outlet,
                roomType: roomType,
                reservationTime : reservationTime,
                reservationDate : reservationDate,
                duration: duration,
                description: desc
            },
            durationValue: hours

        };
    }

    fieldValidation = (values) => {
        let errors = {};
        let re = /^[a-zA-Z0-9.]+@[a-zA-Z0-9]+\.[A-Za-z]+$/;
        if (!values.email) { errors.email = "Username is required" }
        if (values.email && !re.test(values.email)) { errors.email = "Please enter a valid email" }
        if (!values.password) { errors.password = "Password is required" }

        return errors;
    }

    submitForm  = (values, actions) => {
        document.getElementById("formReference").submit();
    }

    render() {
        return (
            <Formik initialValues={this.state.initialValue}
                    validate={this.fieldValidation}
                    onSubmit={this.submitForm}
                    render={({values, errors, status, touched, isSubmitting, form}) => (
                        <Form action={this.state.urlRegister} method="post" id={"formReference"}>
                            <input type="hidden" name="_token" value={csrf_token}/>

                            <div className={"row"}>
                                <div className={"col-sm-10 col-md-8 offset-md-2 offset-sm-1"}>
                                    <label className="label-login">Room Type</label>
                                    <div className="input-group form-group">
                                        <FieldSelectOption name={"roomType"} placeholder={"Room Type"} options={this.state.durationValue}/>
                                        <ErrorMessage name={"roomType"} component="div" className={"errorMessage"}/>
                                    </div>

                                    <div className={"row"}>
                                        <div className={"col-md-6"}>
                                            <label className="label-login">Reservation Date</label>
                                            <div className="input-group form-group">
                                                <FieldText name={"reservationDate"} placeholder={"reservationDate"}/>
                                                <ErrorMessage name={"reservationDate"} component="div" className={"errorMessage"}/>
                                            </div>
                                        </div>

                                        <div className={"col-md-6"}>
                                            <label className="label-login">Reservation Time</label>
                                            <div className="input-group form-group">
                                                <FieldText name={"reservationTime"} placeholder={"reservationTime"}/>
                                                <ErrorMessage name={"reservationTime"} component="div" className={"errorMessage"}/>
                                            </div>
                                        </div>
                                    </div>


                                    <label className="label-login">Phone</label>
                                    <div className="input-group form-group">
                                        <FieldSelectOption name={"duration"} placeholder={"duration"} options={this.state.durationValue}/>
                                        <ErrorMessage name={"duration"} component="div" className={"errorMessage"}/>
                                    </div>

                                    <label className="label-login">Description</label>
                                    <div className="input-group form-group">
                                        <FieldText name={"description"} placeholder={"Note"}/>
                                        <ErrorMessage name={"description"} component="div" className={"errorMessage"}/>
                                    </div>

                                    <div className="form-group">
                                        <br />
                                        <input type="submit" value="RESERVE ROOM" className="btn default_btn btn-primary form-control"/>
                                    </div>

                                </div>
                            </div>
                        </Form>
                    )}
            />

        )
    }
}


class ReservationBooking extends Component {

    constructor() {
        super();

        this.state = {
            isOpen: false
        };

        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.setState({ [event.target.id]: event.target.value });
    }

    render() {
        const { seo_title } = this.state;
        return (
            <div className="form-booking-reservation">
                <div>
                    <ReservationBookingContainer />
                </div>
            </div>
        );
    }
}

export default ReservationBooking;
