import Grow from '@material-ui/core/Grow';

const DefaultShowData = function(props){
    var {item} = props;
    console.log(item);

    var color = "#c67019";
    if (item.reservation_status == "pending"){
        color = "#c67019";
    } else if (item.reservation_status == "success"){
        color = "#0c7b2a";
    } else if (item.reservation_status == "expired"){
        color = "#c11054";
    }

    return (
        <div className={"col-xs-12 col-sm-12 col-md-12 col-lg-12"}>
            <Grow in={true} timeout={2000}>
                <div className={"card_reservation"}>
                    <div className={"card_header"}>
                        <div className={"row"}>
                            <div className={"col-xs-12 col-sm-12 col-md-8 col-lg-10"}>
                                <span style={{padding: "20px", float:"left"}}>{item.outlet_name}</span>
                                <span style={{float: "right", paddingLeft: "20px", paddingBottom: "20px", paddingTop: "20px"}}>{item.reservation_date_time}</span>
                            </div>
                            <div className={"col-xs-12 col-sm-12 col-md-4 col-lg-2"}>
                                <span style={{float: "right", padding: "20px", background: color, borderRadius: "0 20px 0 0"}}>{item.reservation_status}</span>
                            </div>
                        </div>
                    </div>

                    <div className={"card_body"}>
                        <div style={{padding: "20px"}}>{item.room_details.RoomType}, {item.room_details.Pax} pax</div>
                    </div>
                </div>

            </Grow>
            <br />
            <br />
        </div>
    )
}



export default class ReservationListContainer extends React.Component{

    static defaultProps ={
        limitPage:5,
        baseUrl:"api/reservation/view",
        isShowHidden: true,
        isInfiniteScroll: true
    }

    callMoreData = (resetData) => {
        if (this.state.componentStatus != 'loading'){
            console.log("CALL MORE DATA");

            var page = this.state.page;
            var result = this.state.data;
            var dataPerPage = this.state.limitPage;

            if (resetData){
                page = resetData-1;
                result = [];
                this.setState({
                    componentStatus: 'ready'
                });
            }

            if (this.state.componentStatus != 'done'){
                this.setState({
                    componentStatus: 'loading'
                });

                request({
                    url:    this.state.baseUrl,
                    method: 'GET',
                    headers: {
                        Authorization: 'Bearer '+ token
                    },
                    params:{
                        'start':page*dataPerPage,
                        'length':dataPerPage
                    }
                }).then(response => {
                    console.log(response);
                    if (result.length < response.data.data.total){
                        if(page == 0){
                            var urlPath = this.state.baseUrl;
                        }else{
                            var urlPath = this.state.baseUrl+'/'+(page+1);
                        }
                        if (this.state.searchText){
                            urlPath += "?search="+this.state.searchText;
                        }
                        result.push(...response.data.data.data);
                        this.setState({
                            data: result,
                            page: page + 1,
                            componentStatus: 'ready',
                            totalData: response.data.data.length
                        });
                    } else{
                        this.setState({
                            componentStatus: 'done'
                        });
                    }
                })

            }
        }
    }

    constructor(){
        super();
        var page = window.page?window.page:1;
        var searchText = window.search?window.search:"";

        this.state = {
            type: 'card',
            data:[],
            searchText: searchText,
            componentStatus:'ready',

            // Data
            baseUrl:"api/reservation/view",
            ajaxCall:"api/reservation/view",

            // Option
            isShowHidden:false,
            isInfiniteScroll:true,
            isPagination:true,
            limitPage:8,

            // 00pagination related
            page : page,
            totalData: 9999,

        }

        this.callMoreData(true);

    }

    render() {
        return (
            <div className={"reservationList"}>
                <div className={"container"}>
                    <div className={"CatalogCard"}>
                        <DefaultBaseInfiniteTable
                            contentShown={<DefaultShowData />}
                            contentHidden={<span/>}
                            header={<span/>}
                            data={this.state.data}
                            callMoreData={this.callMoreData} {...this.state}/>
                    </div>
                </div>
            </div>
        )
    }
}
