import React from 'react'
import "./index.scss"
import request from "../../../Shared/RequestWrapper.jsx";
import {BrowserRouter} from "react-router-dom";
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';
import { geolocated } from "react-geolocated";

const styles = require('./GoogleMapStyles.json')
const style = {
    width: '100%',
    height: '400px',
    position: 'relative'
}
let allCoords = []

class LandingPageOutletContainer extends React.Component{

    static defaultProps ={
        limitPage:5,
        baseUrl:"api/outlet/list",
        isShowHidden: true,
        isInfiniteScroll: true
    }

    callMoreData = (resetData) => {
        if(this.state.isLoaded==0){
        if (this.state.componentStatus != 'loading') {
            console.log("CALL OUTLET DATA");

            var page = this.state.page;
            var result = this.state.data;
            var dataPerPage = this.state.limitPage;
            var latitude = this.state.latitude;
            var longitude = this.state.longitude;

            if (resetData) {
                page = resetData - 1;
                result = [];
                this.setState({
                    componentStatus: 'ready'
                });
            }

            if (this.state.componentStatus != 'done') {
                this.setState({
                    componentStatus: 'loading'
                });

                console.log(page * dataPerPage);

                request({
                    url: this.state.ajaxCall,
                    method: 'GET',
                    params: {
                        'latitude':latitude,
                        'longitude':longitude,
                        'length': 3
                    }
                }).then(response => {
                    if (result.length < response.data.data.total) {
                        console.log(latitude+" "+longitude);
                        var urlPath = this.state.baseUrl;

                        result.push(...response.data.data.data);
                        this.setState({
                            data: result,
                            componentStatus: 'ready',
                            totalData: response.data.data.length
                        });

                    } else {
                        this.setState({
                            componentStatus: 'done'
                        });
                    }
                })

            }
        }
        this.setState({
            isLoaded: 1
        })
        }
    }

    constructor(){
        super();
        const element = document.getElementById('landingPagePromoContainer')
        const props = Object.assign({}, element.dataset)

        this.state = {
            type: 'card',
            data:[],
            isLoaded: 0,
            loadList: 0,
            componentStatus:'ready',

            // Data
            baseUrl:"api/outlet/list",
            ajaxCall:"api/outlet/list",

            // Option
            isShowHidden:false,
            isInfiniteScroll:true,
            isPagination:true,
            limitPage:8,

            // 00pagination related
            totalData: 9999,

            latitude: '',
            longitude: '',
            mapPosition: {
                lat: '',
                lng: ''
            },
            zoom: 14,

            selectedTab: -1,

            showingInfoWindow: false,
            activeMarker: {},
            selectedPlace: {},

        }


    }

    getLatLng(){
        if(!(this.props.coords===null)){
            this.state.latitude = this.props.coords.latitude
            this.state.longitude = this.props.coords.longitude
            this.state.mapPosition.lat = this.props.coords.latitude
            this.state.mapPosition.lng = this.props.coords.longitude
            this.callMoreData(true);
        }
    }

    getOutletListCoords() {
        allCoords = []
        for (let i=0;i<this.state.data.length;i++) {
            allCoords.push({
                name: this.state.data[i].Name,
                lat: this.state.data[i].outletLat,
                lng: this.state.data[i].outletLong,
            })
        }
    }

    onMarkerClick = (props, marker, e) =>
        this.setState({
            selectedPlace: props,
            activeMarker: marker,
            showingInfoWindow: true
        });

    goToLocation = (index) => {
        this.setState({
            mapPosition: {
                lat: allCoords[index].lat,
                lng: allCoords[index].lng
            },
            selectedTab: index
        });
    }

    render() {
        if(!(this.state.latitude && this.state.longitude)){
            this.getLatLng()
        }

        this.getOutletListCoords()

        return (
            <div className="nearestOutlet">
                <ul className="outletData nav nav-tabs">
                    {allCoords.map((data,index) => (
                        <li className="nav-item">
                            {console.log('data')}
                            {console.log(data.selected)}
                            <a className= {this.state.selectedTab==index ? "text-white nav-link active" : "text-white nav-link"} onClick={() =>this.goToLocation(index)}>{data.name}</a>
                        </li>
                    ))}
                </ul>
                <Map google={this.props.google} zoom={14}
                     initialCenter={{
                         lat:this.state.mapPosition.lat,
                         lng:this.state.mapPosition.lng
                     }}
                     center={{
                         lat:this.state.mapPosition.lat,
                         lng:this.state.mapPosition.lng
                     }}
                     styles={styles}

                >

                    <Marker onClick={this.onMarkerClick}
                            name={'Current location'}
                    />

                    {allCoords.map((data,index) => (
                        <Marker onClick={this.onMarkerClick}
                            name={data.name}
                            position={{lat: data.lat, lng: data.lng}}
                        />

                    ))}

                    <InfoWindow
                        marker={this.state.activeMarker}
                        visible={this.state.showingInfoWindow}>
                        <div>
                            <p>{this.state.selectedPlace.name}</p>
                        </div>
                    </InfoWindow>
                </Map>
            </div>
        )
    }
}


export default geolocated({
    positionOptions: {
        enableHighAccuracy: false,
    },
    userDecisionTimeout: 5000,
})(GoogleApiWrapper({
    apiKey: ("AIzaSyCscymp_ef8JBSfCvQTS9I3lleT2oELRTc")
})(LandingPageOutletContainer))