import React from 'react'
import "./index.scss"
import request from "../../../Shared/RequestWrapper.jsx";
import {BrowserRouter} from "react-router-dom";

const DefaultShowData = function(props){
    console.log(props);
    var item = props;


    const allData = [];
    const firstData = [];
    if(!(item.props[0]===undefined)){
        if (item.props[0].Photo == null) item.props[0].Photo = "uploads/Empty.jpg";
        firstData.push({
            image: item.props[0].Photo,
            title: item.props[0].Name,
            desc : item.props[0].Description,
            ID : item.props[0].ID
        })
    }
    for (let i=1;i<item.props.length;i++) {
        if (item.props[i].Photo == null) item.props[i].Photo = "uploads/Empty.jpg";
        allData.push({
            image: item.props[i].Photo,
            title: item.props[i].Name,
            desc : item.props[i].Description,
            ID : item.props[i].ID
        })
    }
    return (
        <div className={"promoList"}>
            <div className="promoSection col-lg-12 col-md-12 col-sm-12 col-xs-12">
                {firstData.map((data,index) => (
                    <div className="promoBanner row">
                        <div className="col-lg-12">
                            <a href={"/promo/detail/"+data.ID}>
                                <img src={"/"+data.image}/>
                            </a>
                            <br />
                        </div>
                        <div className="col-lg-12">
                            <div className="content col-xs-12">
                                <h1 className="contentTitle">{data.title}</h1>
                                <p className="contentDesc"><div dangerouslySetInnerHTML={{__html: data.desc}}/> </p>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
            <hr/>

            <div className="row">
                {allData.map((data,index) => (
                    <div className="col-lg-3 col-md-4 col-sm-6 col-xs-12">
                        <div className="promoCard">
                            <a href={"/promo/detail/"+data.ID}>
                                <img src={"/"+data.image}/>
                                <p className="title">{data.title}</p>
                            </a>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
}

export default class KeepingBottleContainer extends React.Component{

    static defaultProps ={
        limitPage:5,
        baseUrl:"api/promo/list",
        isShowHidden: true,
        isInfiniteScroll: true
    }


    callMoreData = (resetData) => {
        if (this.state.componentStatus != 'loading'){
            console.log("CALL MORE DATA");

            var page = this.state.page;
            var result = this.state.data;
            var dataPerPage = this.state.limitPage;

            if (resetData){
                page = resetData-1;
                result = [];
                this.setState({
                    componentStatus: 'ready'
                });
            }

            if (this.state.componentStatus != 'done'){
                this.setState({
                    componentStatus: 'loading'
                });

                console.log(page*dataPerPage);

                request({
                    url:    this.state.ajaxCall,
                    method: 'GET',
                    params:{
                    }
                }).then(response => {
                    if (result.length < response.data.data.total){
                        if(page == 0){
                            var urlPath = this.state.baseUrl;
                        }else{
                            var urlPath = this.state.baseUrl+'/'+(page+1);
                        }
                        result.push(...response.data.data.data);
                        this.setState({
                            data: result,
                            componentStatus: 'ready',
                            totalData: response.data.data.length
                        });
                    } else{
                        this.setState({
                            componentStatus: 'done'
                        });
                    }
                })

            }
        }
    }


    constructor(){
        super();
        const element = document.getElementById('helpContainer')
        const props = Object.assign({}, element.dataset)

        this.state = {
            type: 'card',
            data:[],
            componentStatus:'ready',

            // Data
            baseUrl:"api/promo/list",
            ajaxCall:"api/promo/list",

            // Option
            isShowHidden:false,
            isInfiniteScroll:true,
            isPagination:true,
            limitPage:8,

            // 00pagination related
            totalData: 9999,

        }

        this.callMoreData(true);
    }

    render() {
        return (
            <div className={"container"}>
                <DefaultShowData props={this.state.data}/>
            </div>
        )
    }
}