import React from 'react'
import "./index.scss"
import request from "../../../Shared/RequestWrapper.jsx";
import {BrowserRouter} from "react-router-dom";
import DefaultBaseModal from "../../../Components/00Default/03Popup/00defaultBaseModal.jsx"
import ReceiptDetailContainer from "./receiptDetailContainer.jsx";

const DefaultShowData = function(props){
    console.log(props);
    var item = props;
    const allData = [];
    for (let i=0;i<item.props.length;i++) {
        allData.push({
            invoice: item.props[i].Invoice,
            date: item.props[i].Date,
            outlet : item.props[i].Outlet
        })
    }
    return (
        <div className={"receiptList"}>
            <div className="receiptSection col-lg-12 col-md-12 col-sm-12 col-xs-12">
                {allData.map((data,index) => (
                    <div className="receiptItem" onClick={props.toggleModal}>
                        <div className="col-md-12 row">
                            <div className="col-md-6">
                                <b>{data.invoice}</b>
                            </div>
                            <div className="col-md-6">
                                <p className="outlet-name"> {data.outlet}</p>
                            </div>
                        </div>
                        <div className="col-md-12">
                            {data.date}
                        </div>
                        <div className="col-md-12">
                            <hr/>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
}

export default class ReceiptListContainer extends React.Component{

    static defaultProps ={
        limitPage:5,
        baseUrl:"http://13.76.167.131:3011/mbr/110819031007",
        isShowHidden: true,
        isInfiniteScroll: true
    }


    callMoreData = (resetData) => {
        if (this.state.componentStatus != 'loading'){
            console.log("CALL MORE DATA");
            console.log(this.state.ajaxCall)

            var result = this.state.data;

            if (resetData){
                result = [];
                this.setState({
                    componentStatus: 'ready'
                });
            }

            if (this.state.componentStatus != 'done'){
                this.setState({
                    componentStatus: 'loading'
                });

                request({
                    url:    this.state.ajaxCall,
                    method: 'GET',
                    headers: {
                        'Accept': 'text/plain',
                    },
                    params:{
                    }
                }).then(response => {
                    if (result.length < response.data.length){
                        result.push(...response.data.data);
                        this.setState({
                            data: result,
                            componentStatus: 'ready',
                            totalData: response.data.length
                        });
                    } else{
                        this.setState({
                            componentStatus: 'done'
                        });
                    }
                })

            }
        }
    }

    toggleModal = ()=>{
        this.setState({
            isOpen:!this.state.isOpen
        });
    }


    constructor(){
        super();
        const element = document.getElementById('receiptListContainer')
        const props = Object.assign({}, element.dataset)

        this.state = {
            type: 'card',
            // data:[{"Outlet":"HP043","Reception":"RCP-1904130012","Invoice":"IVO-1904130012","Date":"2019-04-13T13:11:27.000Z","Member":"110819031007","Nama":"NI KADEK NINA PRADANI"},
            //     {"Outlet":"HP108","Reception":"RCP-1903200011","Invoice":"IVC-1903200011","Date":"2019-03-20T17:26:28.000Z","Member":"110819031007","Nama":"NI KADEK NINA PRADANI"},
            //     {"Outlet":"HP108","Reception":"RCP-1903260043","Invoice":"IVC-1903260043","Date":"2019-03-26T20:26:30.000Z","Member":"110819031007","Nama":"NI KADEK NINA PRADANI"},
            //     {"Outlet":"HP108","Reception":"RCP-1903290047","Invoice":"IVC-1903290047","Date":"2019-03-29T19:45:53.000Z","Member":"110819031007","Nama":"NI KADEK NINA PRADANI"},
            //     {"Outlet":"HP108","Reception":"RCP-1904010050","Invoice":"IVC-1904010050","Date":"2019-04-01T20:27:08.000Z","Member":"110819031007","Nama":"NI KADEK NINA PRADANI"},
            //     {"Outlet":"HP108","Reception":"RCP-1905010085","Invoice":"IVC-1905010085","Date":"2019-05-01T21:26:08.000Z","Member":"110819031007","Nama":"NI KADEK NINA PRADANI"}],
            data: [],
            componentStatus:'ready',

            // Data
            baseUrl:"http://13.76.167.131:3011/mbr/110819031007",
            ajaxCall:"http://13.76.167.131:3011/mbr/110819031007",

            // Option
            isShowHidden:false,
            isInfiniteScroll:true,
            isPagination:true,
            limitPage:8,

            // 00pagination related
            totalData: 9999,
            isOpen: false

        }

        this.callMoreData(true);
    }

    render() {
        return (
            <div className={"container"}>
                <DefaultShowData props={this.state.data} toggleModal={this.toggleModal} />
                <DefaultBaseModal isOpen={this.state.isOpen} closeModal={this.toggleModal.bind(this)}
                                  content={<ReceiptDetailContainer baseUrl={"/"+this.props.baseUrl+"/delete/"} method={"post"} />} />
            </div>
        )
    }
}

