import React from 'react'
import "./index.scss"
import DemoCarousel from "~/0Template/Components/00Default/06Carousel/00demoCarousel.jsx"
import request from "../../../Shared/RequestWrapper.jsx";

export default class LandingPageSliderContainer extends React.Component{

    static defaultProps ={
        limitPage:5,
        baseUrl:"api/news/view",
        isShowHidden: true,
        isInfiniteScroll: true
    }


    loadData = () => {
        if (this.state.componentStatus != 'loading') {
            console.log(this.state.baseUrl);
            if (this.state.componentStatus != 'done') {
                this.setState({
                    componentStatus: 'loading'
                });

                request({
                    url:    this.state.baseUrl,
                    method: 'GET',
                }).then(response => {
                    console.log(response.data.data);
                    this.setState({
                        componentStatus: 'ready',
                        newsData: response.data.data.data
                    });
                })

            }
        }
    }


    constructor(){
        super();
        const element = document.getElementById('landingPageSliderContainer')
        const props = Object.assign({}, element.dataset)

        this.state = {
            slideData:[{
                id: 1,
                image: 'https://mdbootstrap.com/img/Photos/Others/images/79.jpg',
                title : 'Title 1'
            }, {
                id: 1,
                image: 'https://mdbootstrap.com/img/Photos/Others/images/47.jpg',
                title : 'Title 2'
            }],
            componentStatus:'ready',
        }
    }

    render() {
        return (
            <div className={"landingPageSlider"}>
                <DemoCarousel data={this.state.slideData} showStatus={false} showThumbs={false}/>
            </div>
        )
    }
}
