import React from 'react'
import "./index.scss"
import DefaultBaseInfiniteTable from "../../../Components/01Table/00defaultBaseInfiniteTable.jsx";
import request from "../../../Shared/RequestWrapper.jsx";
import Button from '@material-ui/core/Button';
import { Link, BrowserRouter } from 'react-router-dom'
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Typography from '@material-ui/core/Typography';
import Grow from '@material-ui/core/Grow';

const DefaultShowData = function(props){
    var {item} = props;
    console.log(item);
    if (item.Photo == "null") item.Photo = "uploads/Empty.jpg";
    if (item.Photo == null) item.Photo = "uploads/Empty.jpg";

    return (
        <div className={"col-lg-3 col-md-4 col-sm-6 col-xs-12"}>
            <Grow in={true} timeout={2000}>
                <Card className={""}>
                    <CardMedia
                        style={{
                            height: "250px"
                        }}
                        className={"media"}
                        image={"/"+item.Photo}
                        title={item.Name} />
                    <CardContent>
                        <Typography noWrap component={"h6"}>{item.Name} ({item.Code})</Typography>
                        <Typography noWrap component={"body"}>{item.Address} {item.City}</Typography>
                    </CardContent>
                    <CardActions style={{paddingLeft: "12px", paddingRight: "12px"}}>
                        <BrowserRouter>
                            <Button className={"linkButton"} variant="contained" size="small" color="primary" href={"/outlet/detail/"+item.ID}>
                                View More
                            </Button>
                        </BrowserRouter>
                    </CardActions>
                </Card>
            </Grow>
            <br />
        </div>
    )
}



export default class OutletListContainer extends React.Component{

    static defaultProps ={
        limitPage:5,
        baseUrl:"api/outlet/list",
        isShowHidden: true,
        isInfiniteScroll: true
    }

    callMoreData = (resetData) => {
        if (this.state.componentStatus != 'loading'){
            console.log("CALL MORE DATA");

            var page = this.state.page;
            var result = this.state.data;
            var dataPerPage = this.state.limitPage;

            if (resetData){
                page = resetData-1;
                result = [];
                this.setState({
                    componentStatus: 'ready'
                });
            }

            if (this.state.componentStatus != 'done'){
                this.setState({
                    componentStatus: 'loading'
                });

                request({
                    url:    this.state.baseUrl,
                    method: 'GET',
                    params:{
                        'search[value]': this.state.searchText,
                        'search[regex]': false,
                        'start':page*dataPerPage,
                        'length':dataPerPage
                    }
                }).then(response => {
                    if (result.length < response.data.data.total){
                        if(page == 0){
                            var urlPath = this.state.baseUrl;
                        }else{
                            var urlPath = this.state.baseUrl+'/'+(page+1);
                        }
                        if (this.state.searchText){
                            urlPath += "?search="+this.state.searchText;
                        }
                        result.push(...response.data.data.data);
                        this.setState({
                            data: result,
                            page: page + 1,
                            componentStatus: 'ready',
                            totalData: response.data.data.length
                        });
                    } else{
                        this.setState({
                            componentStatus: 'done'
                        });
                    }
                })

            }
        }
    }

    constructor(){
        super();
        var page = window.page?window.page:1;
        var searchText = window.search?window.search:"";

        this.state = {
            type: 'card',
            data:[],
            searchText: searchText,
            componentStatus:'ready',

            // Data
            baseUrl:"api/outlet/list",
            ajaxCall:"api/outlet/list",

            // Option
            isShowHidden:false,
            isInfiniteScroll:true,
            isPagination:true,
            limitPage:8,

            // 00pagination related
            page : page,
            totalData: 9999,

        }

        this.callMoreData(true);

    }

    render() {
        return (
            <div className={"outletList"}>
                <div className={"container"}>
                    <div className={"CatalogCard"}>
                        <div id="material-tabs">
                            <a id="tab1-tab" href="#tab1" className="active">Nearby</a>
                            <a id="tab2-tab" href="#tab2">Surabaya</a>
                            <a id="tab3-tab" href="#tab3">Malang</a>
                            <a id="tab4-tab" href="#tab4">Sidoarjo</a>
                            <a id="tab5-tab" href="#tab5">All</a>
                            <span className="yellow-bar"></span>
                        </div>

                        <DefaultBaseInfiniteTable
                            contentShown={<DefaultShowData />}
                            contentHidden={<span/>}
                            header={<span/>}
                            data={this.state.data}
                            callMoreData={this.callMoreData} {...this.state}/>
                    </div>
                </div>
            </div>
        )
    }
}
