import React from 'react'
import "./index.scss"
import DemoCarousel from "~/0Template/Components/00Default/06Carousel/00demoCarousel.jsx"
import request from "../../../Shared/RequestWrapper.jsx";

export default class NewsDetailContainer extends React.Component{

    static defaultProps ={
        limitPage:5,
        baseUrl:"api/promo/view",
        isShowHidden: true,
        isInfiniteScroll: true
    }


    loadData = () => {
        if (this.state.componentStatus != 'loading') {
            console.log(this.state.baseUrl);

            var id = this.state.promo_detail_id;

            if (this.state.componentStatus != 'done') {
                this.setState({
                    componentStatus: 'loading'
                });

                request({
                    url:    this.state.baseUrl,
                    method: 'GET',
                }).then(response => {
                    console.log(response.data.data);
                    this.setState({
                        componentStatus: 'ready',
                        promoData: response.data.data.data
                    });
                })

            }
        }
    }


    constructor(){
        super();

        var promo_detail_id = window.promo_detail_id ? window.promo_detail_id : 0;

        const element = document.getElementById('promoDetailContainer')
        const props = Object.assign({}, element.dataset)

        this.state = {
            slideData:[{
                id: 1,
                image: 'http://react-responsive-carousel.js.org/assets/3.jpeg',
                title : 'Title 1'
            }],
            componentStatus:'ready',

            // Data
            promoData: {},
            promo_detail_id: promo_detail_id,
            baseUrl:"api/promo/view/"+promo_detail_id,

        }

        this.loadData();
    }

    render() {
        return (
            <div className={"promoDetail"}>
                <header className="text-white headerBG">
                    <div className="text-center">
                        <h2 className="promoTitle">{this.state.promoData.Name}</h2>
                    </div>
                </header>
                <div className={"container-detail-promo container"}>
                    <div className={"banner-detail-promo"} style={{  backgroundImage: "url(/"+this.state.promoData.Photo+")",
                        backgroundPosition: 'center',
                        backgroundSize: 'contain',
                        backgroundRepeat: 'no-repeat',
                        height:'400px'}}></div>
                    <br />
                    <div className="content-detail-promo col-md-12 row">
                        <div className="content-detail-promo col-md-9">
                            <p><div dangerouslySetInnerHTML={{__html: this.state.promoData.Description}}/></p>
                        </div>
                        <div className="content-detail-promo available-promo col-md-3">
                            <p className="available-promo-title">Available Promo</p>
                            <p>{this.state.promoData.publishDate}</p>
                        </div>
                    </div>

                </div>
                <br />
            </div>
        )
    }
}
